import React from "react";
import "./Chat.css";
import { Avatar, IconButton } from "@mui/material";
import {
  AttachFile,
  InsertEmoticon,
  MoreVert,
  SearchOutlined,
} from "@mui/icons-material";
import { useSelector } from "react-redux";

const Chat = () => {
  let names = useSelector((state) => state.name);
  let name = names || "Abby";
  return (
    <div className="chat">
      <div className="chat__header">
        <Avatar
          src={`https://api.dicebear.com/7.x/personas/svg?seed=${name}`}
          alt="avatar"
        />
        <div className="chat__headerInfo">
          <h2>{name}</h2>
          <p>last seen {new Date().toString().slice(0, 25)}</p>
        </div>
        <div className="chat__headerRight">
          <IconButton>
            <SearchOutlined />
          </IconButton>
          <IconButton>
            <AttachFile />
          </IconButton>
          <IconButton>
            <MoreVert />
          </IconButton>
        </div>
      </div>
      <div className="chat__body">
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          Hello! Stark
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          Hello! {name}
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          May i know your personal deails for form filling
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          sure.
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          Your full name  is?
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          Tony Stark
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          mail id please.
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          tonystark123@gmail.com
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          Your phone number?
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          91-9988776655
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
        <p className="chat__message">
          <span className="chat__name">{name}</span>
          Thank you! I will contact you soon...
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}<p className="chat__message chat__receiver">
          <span className="chat__name">you</span>
          Great to hear that! Have a great day!
          <span className="chat__timestamp">
            {new Date().toString().slice(0, 25)}
          </span>{" "}
        </p>{" "}
      </div>
      <div className="chat__footer">
        <InsertEmoticon />
        <form>
          <input
            type="text"
            onChange={(e) => e.preventDefault()}
            placeholder="Type a message..."
          />
          <button>send a message</button>
        </form>
      </div>
    </div>
  );
};

export default Chat;
