import React from "react";
import "./Sidebar.css";
import { Avatar, IconButton } from "@mui/material";
import {
  Chat,
  DonutLarge,
  MoreVert,
  SearchOutlined,
} from "@mui/icons-material";
import SidebarChat from "../SidebarChat/SidebarChat";

const Sidebar = () => {
  
  let types=[ "Dusty","Abby","Bubba","Luna","Cali","Bob","Felix","Tiger","Peanut"]

  return (
    <div className="sidebar">
      <div className="sidebar__header">
        <Avatar src="https://api.dicebear.com/7.x/personas/svg?seed=Molly" />
        <h1 className="profilename">Tony Stark</h1>
        <div className="sidebar__headerRight">
          <IconButton>
            <DonutLarge />
          </IconButton>
          <IconButton>
            <Chat />
          </IconButton>
          <IconButton>
            <MoreVert />
          </IconButton>
        </div>
      </div>
      <div className="sidebar__search">
        <div className="sidebar__searchContainer">
          <SearchOutlined />

          <input type="text" placeholder="Search or start new chat" />
        </div>
      </div>
      <div className="sidebar__chats">
      {types.map((a,b)=>{
        return   <SidebarChat name={a} id={b} />
      })}
      </div>
    </div>
  );
};

export default Sidebar;
