import { createSlice } from "@reduxjs/toolkit";
var initialState = "Dusty";
const showname = createSlice({
  name: "name",
  initialState,
  reducers: {
    setname(state, action) {
      return (state = action.payload);
    },
  },
});

export const { setname } = showname.actions;
export default showname.reducer;
